import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import img from './img/shoe.png';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { lazy, Suspense, createContext, useState } from 'react';
import data from './data.js'
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'
import React from 'react';
import axios from 'axios';
import { useQuery } from 'react-query';

export let Context1 = createContext()


const Detail = lazy(() => import('./routes/detail.js'));;
const Cart = lazy(() => import('./routes/Cart.js'));;

function App() {

  let obj = {name : 'lee'}
  localStorage.setItem('data', JSON.stringify(obj))

  let 꺼낸거 = localStorage.getItem('data')
  console.log(JSON.parse(꺼낸거).name);



  let [adidas, setAdidas] = useState(data)
  let [stock] = useState([10, 11, 12])
  let navigate = useNavigate(); //페이지 이동 도와줌

  let result = useQuery('name', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json').then((a)=>{
      return a.data
    })
  })
  
  // result.data //요청이 성공했을 때 가져오는 데이터 들어있음
  // result.isLoading //요청이 로딩중일 때 true
  // result.error //요청이 실패했을 때 true

  return (
    <div className="App">

      <Navbar bg="black" data-bs-theme="dark" className='bar'>
        <Container>
          <Navbar.Brand onClick={()=>{ navigate('/') }}>adidas</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="#home">home</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/Cart') }}>Cart</Nav.Link>
          </Nav>
          <Nav className='ms-auto' style={{ color : "white" }}>
            { result.isLoading && '로딩중' }
            { result.error && '에러남' }
            { result.data && result.data.name }
          </Nav>
        </Container>
      </Navbar>

      {/* <Link to="/">홈</Link>
      <Link to="/detail">상세페이지</Link>
      <Link to="/about">어바웃페이지</Link> */}

    <Suspense fallback={<div>로딩중임</div>}>
      <Routes>
        <Route path="/" element={
          <>
            <div className='main-bg' style={{ backgroundImage : 'url('+ img +')' }}></div>

            <Container>
            <Row>
              {/* <Card adidas = {adidas[0]} i={1}/>
              <Card adidas = {adidas[1]} i={2}/>
              <Card adidas = {adidas[2]} i={3}/> */}
              {
                adidas.map((a, i)=>{
                  return (
                    <Card adidas = {adidas[i]} i={i}/>
                  )
                })
              }
            </Row>
          </Container>
          <button onClick={()=>{
            axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((결과)=>{
              let copy = [...adidas, ...결과.data];
              setAdidas(copy);
            })

            Promise.all([ axios.get('/url1'), axios.get('/url2')])
            .then(()=>{
              //두개의 url에 보낸 get요청이 전부 성공했을 때 실행코드
            })

          }}>더보기</button>
          </>
        } />

        <Route path="/detail/:id" element={
          <Context1.Provider value={{stock}}>
        <Detail adidas={adidas}/>
        </Context1.Provider>
        } />

        <Route path="/cart" element={ <Cart/> } />
                

        <Route path="/about" element={<About/>}>
        <Route path="member" element={<div>멤버</div>} />
        <Route path="location" element={<div>위치</div>} />
        </Route>

        <Route path="*" element={<div>없는페이지예요</div>} /> 

      </Routes>
      </Suspense>
    
    </div>
  );
}

function About(){
  return (
    <div>
      <h4>회사정보</h4>
      <Outlet></Outlet>
    </div>
  )
}

function Card(props){
  return (
  <Col>
  <img src={process.env.PUBLIC_URL + '/img/Simg' +(props.i+1)+'.png'} width="50%"/>
  <h5>{props.adidas.title}</h5>
  <p>{props.adidas.content}</p>
  <p>{props.adidas.price}</p>
  </Col>
  )
}






export default App;
