import { createSlice } from "@reduxjs/toolkit";


let user = createSlice({
    name : 'user',
    initialState : { name : 'lee', age : 25 },
    reducers : {
        changeName(state){
            state.name = 'jiyoung lee'
        },
        increase(state, action){
            state.age += action.payload
        },
    }
})


export let { changeName, increase } = user.actions


export default user